.title {
  border-top: 2px solid #f6f5c5;
  border-bottom:  2px solid #f6f5c5;
  display: flex;
  align-items: center;
  margin: 33px 0;
  width: 100%;
  &__decor {
    width: 24px;
    height: 24px;
    background: #f6f5c5; }
  &__text {
    font-size: 31px;
    font-weight: 500;
    letter-spacing: 8px;
    line-height: 90px;
    margin: 0 15px; } }
