.hamburger-button {
  display: none; }

.header {
  z-index: 999;
  position: fixed;
  width: 100vw;
  top: 0;
  left: 0;
  background: #666666;
  transition: .5s;
  .container {
    position: relative; }
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100px;
    transition: .5s; }
  &.scroll {
    padding: 0;
    .header {
      &__container {
        top: 0;
        left: 0;
        align-items: center;
        height: 70px; } }
    .logo {
      &__header {
        font-size: 30px;
        line-height: 30px; }
      &__footer {
        align-items: center; }
      &__footer-text {
        font-size: 14px;
        color: #f6f5c5; } } } }


.logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  &__header {
    font-size: 60px;
    line-height: 50px;
    color: #ffc06f;
    transition: .5s; }
  &__footer {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    width: 100%; }
  &__footer-text {
    font-size: 28px;
    color: #f6f5c5;
    transition: .5s; } }

.nav {
  display: flex;
  align-items: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
  &.active {
    display: flex; }
  &__part {
    font-size: 18px;
    line-height: 45px;
    padding: 0 20px;
    font-weight: 500;
    letter-spacing: 6px;
    text-decoration: none;
    color: inherit;
    border-top: 3px solid #cecece;
    border-bottom: 3px solid #cecece;
    background: #fff;
    &:hover, &.active {
      color: #ffc06f;
      border-top: 3px solid #ffc06f;
      border-bottom: 3px solid #ffc06f; } } }
