.biography-page {
  &__hero {
    width: 100%;
    margin-top: 70px; } }
.biography-page-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__title {
    margin: 27px 0 60px;
    text-align: center; }
  &__title-header {
    font-size: 61px; }
  &__title-footer {
    font-size: 29px; }
  &__main {
    line-height: 40px;
    font-size: 29px;
    margin-bottom: 160px; }
  &__text {
    margin: 0; } }
